import BgCan from "@/components/particles";
import "./index.css";
import logo from "@/assets/img/loserking.png";
import Bottom from "@/components/bottom";
import te from "./te.png";
import tw from "./tw.png";
import { useTranslation } from "react-i18next";
function App() {
  const { t, i18n } = useTranslation();
  const language: any = {
    zhCN: {
      title: "简体-中文",
      code: "zhCN",
    },
    enUS: {
      title: "English",
      code: "enUS",
    },
  };

  return (
    <div className="comp w-full flex flex-col">
      <div className="w-[80%] max-w-[1200px] absolute left-1/2 -translate-x-1/2 top-[24px] text-right">
        <span
          className=" px-[8px] py-[4px] cursor-pointer border-[#1e4151] border-[1px] rounded-[2px] text-[14px] hover:opacity-80"
          onClick={() => {
            i18n.changeLanguage(i18n.language.includes("US") ? "zhCN" : "enUS");
          }}
        >
          {language[i18n.language].title}
        </span>
      </div>
      <div className="content w-full h-screen bg-[#f8f8f8]">
        <BgCan />
        <div className="flex flex-col absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2">
          <div className="min-w-[300px] flex flex-col pb-[32px] items-center">
            <div className="text-center mb-[26px] text-[32px] text-[#1e4151] font-bold">
              {t("home.title")}
            </div>
            <img src={logo} alt="" className="w-[80px] h-[80px]" />
            <div className="mt-[16px] text-[16px] text-[#1e4151] text-center">
              {t("home.intro")}
            </div>
          </div>
          <div className="flex flex-col md:flex-row justify-center items-center">
            <a
              href="#desc"
              className="menu-btn menu-btn-primary mb-[10px] md:mb-0"
            >
              {t("home.btn-a")}
            </a>
            <a
              href="#group"
              className="menu-btn menu-btn-ghost flex items-center"
            >
              <img src={te} alt="" className="w-[20px] h-[20px]" />
              <p className="px-[4px]">{t("home.btn-b")}</p>
            </a>
          </div>
        </div>
      </div>
      <div className="container mx-auto py-[32px] flex flex-col items-center px-[24px]">
        <div id="desc" className="py-[24px]">
          <div className="text-[22px] mb-[24px] text-center">
            {t("home.block-a.title")}
          </div>
          <div className="flex flex-col">
            <div className="text-[18px] mb-[12px]">
              {t("home.block-a.intro")}
            </div>
            <div className="text-[18px] mb-[6px]">
              {t("home.block-a.list.title")}
            </div>
            <div className="text-[18px]">
              <p className="py-[2px]">{t("home.block-a.list.A")}</p>
              <p className="py-[2px]">{t("home.block-a.list.B")}</p>
              <p className="py-[2px]">{t("home.block-a.list.C")}</p>
              <p className="py-[2px]">{t("home.block-a.list.D")}</p>
            </div>
          </div>
        </div>
        <div className="pt-[24px]">
          <div id="group" className="text-[22px] mb-[24px] text-center">
            {t("home.block-b.title")}
          </div>
          <div className="flex justify-center mb-[24px]">
            <div
              className="px-[12px] cursor-pointer hover:opacity-80"
              onClick={() =>
                window.open(
                  "https://twitter.com/loserking99999x?s=11&t=s0oW7E2NzraoH_sOnDRPGA"
                )
              }
            >
              <img
                src={tw}
                alt=""
                title={t("home.block-b.btn-a")}
                className="w-[40px] h-[40px]"
              />
            </div>
            <div
              className="px-[12px] cursor-pointer hover:opacity-80"
              onClick={() => window.open("https://t.me/LoserKing_official")}
            >
              <img
                src={te}
                alt=""
                title={t("home.block-b.btn-b")}
                className="w-[40px] h-[40px]"
              />
            </div>
          </div>
          <div>
            <p className="mb-[6px] text-[16px] text-[#333]">
              {t("home.block-b.intro")}
            </p>
            <p className="text-right text-[14px] text-[#444]">
              {t("home.block-b.author")}
            </p>
          </div>
        </div>
      </div>
      <Bottom />
    </div>
  );
}

export default App;
