import Particles from "react-tsparticles";
import { loadFull } from "tsparticles";
import imgA from "@/assets/img/loserking.png";

const App = () => {
  const particlesInit = async (main: any) => {
    // you can initialize the tsParticles instance (main) here, adding custom shapes or presets
    // this loads the tsparticles package bundle, it's the easiest method for getting everything ready
    // starting from v2 you can add only the features you need reducing the bundle size
    await loadFull(main);
  };

  const particlesLoaded: any = (container: any) => {};
  const optionsData: any = {
    fullScreen: false,
    fpsLimit: 60,
    particles: {
      number: {
        value: 12,
        density: {
          enable: true,
          value_area: 800,
        },
      },
      shape: {
        type: ["image"],
        image: {
          src: imgA,
          width: 50,
          height: 50,
        },
      },
      opacity: {
        value: 1,
        random: false,
      },
      size: {
        value: { max: 25, min: 5 },
      },
      line_linked: {
        enable: true,
        distance: 150,
        color: "#808080",
        opacity: 0.4,
        width: 1,
      },
      move: {
        enable: true,
        speed: 3,
        direction: "none",
        random: false,
        straight: false,
        out_mode: "out",
        bounce: false,
        attract: {
          enable: false,
          rotateX: 600,
          rotateY: 1200,
        },
      },
    },
    interactivity: {
      detect_on: "canvas",
      events: {
        onhover: {
          enable: true,
          mode: "grab",
        },
        onclick: {
          enable: true,
          mode: "push",
        },
        resize: true,
      },
      modes: {
        grab: {
          distance: 150,
        },
        push: {
          particles_nb: 3,
        },
      },
    },
    retina_detect: true,
  };
  return (
    <Particles
      init={particlesInit}
      loaded={particlesLoaded}
      className="w-full h-full"
      options={optionsData}
    />
  );
};

export default App;
