import i18n from "i18next";
import { initReactI18next } from "react-i18next";

import enUS from "./en-us.json";
import zhCN from "./zh-cn.json";

const resources = {
  enUS: {
    translation: enUS,
  },
  zhCN: {
    translation: zhCN,
  },
};

i18n.use(initReactI18next).init({
  resources,
  lng: "enUS",
  fallbackLng: "enUS",
  interpolation: {
    escapeValue: false,
  },
});

export default i18n;
